'use client';
import React, { useEffect, useState } from 'react';
import { usePathname } from 'next/navigation';
import Link from 'next/link';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import IconButton from '@mui/material/IconButton';
import Drawer from '@mui/material/Drawer';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import MenuIcon from '@mui/icons-material/Menu';
import { BUTTON_HOVER_STYLES } from '@/constants/buttonColors';

const Header = () => {
  const [activeSection, setActiveSection] = useState('home');
  const [mobileOpen, setMobileOpen] = useState(false);
  const pathname = usePathname();

  // Update active section based on scroll position
  useEffect(() => {
    const handleScroll = () => {
      const sections = ['home', 'about', 'contact'];
      const scrollPosition = window.scrollY + 100;

      for (const section of sections) {
        const element = document.getElementById(section);
        if (element) {
          const offsetTop = element.offsetTop;
          const offsetHeight = element.offsetHeight;

          if (scrollPosition >= offsetTop && scrollPosition < offsetTop + offsetHeight) {
            setActiveSection(section);
            break;
          }
        }
      }
    };

    // Initial check
    handleScroll();
    
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  // Smooth scroll to section
  const scrollToSection = (sectionId) => {
    const element = document.getElementById(sectionId);
    if (element) {
      window.scrollTo({
        top: element.offsetTop - 80, // Adjust for header height
        behavior: 'smooth'
      });
    }
  };

  const handleDrawerToggle = () => {
    setMobileOpen((prev) => !prev);
  };

  // Check if current path matches the section
  const isActive = (section) => {
    if (pathname === '/') {
      return activeSection === section;
    }

    return (section === 'home' && pathname === '/') ||
           (section !== 'home' && pathname.includes(section));
  };

  return (
    <Box 
      component="header"
      sx={{ 
        backgroundColor: 'primary.main',
        position: 'fixed',
        width: '100%',
        top: 0,
        zIndex: 1300,
        boxShadow: '0px 2px 4px rgba(0,0,0,0.2)',
      }}
    >
      <Toolbar>
        <Typography variant="h6" component="div" sx={{ flexGrow: 1, color: 'white' }}>
          Logo
        </Typography>
        <Box sx={{ 
          display: { 
            xs: 'none',
            sm: 'flex' 
          }, 
          gap: 2, 
          mr: 2 
        }}>
          <Button 
            onClick={() => scrollToSection('home')}
            sx={{ 
              color: 'white',
              fontSize: {
                xs: '0.875rem',
                sm: '1rem'
              },
              position: 'relative',
              '&:hover': {
                backgroundColor: 'rgba(255, 255, 255, 0.1)',
                transform: 'translateY(-1px)',
                transition: 'all 0.3s ease'
              },
              '&:after': (isActive('home')) ? {
                content: '""',
                position: 'absolute',
                bottom: '-5px',
                left: '50%',
                transform: 'translateX(-50%)',
                width: '30px',
                height: '3px',
                backgroundColor: 'white',
                borderRadius: '2px'
              } : {}
            }}
          >
            Home
          </Button>
          <Button 
            onClick={() => scrollToSection('about')}
            sx={{ 
              color: 'white',
              fontSize: {
                xs: '0.875rem',
                sm: '1rem'
              },
              position: 'relative',
              '&:hover': {
                backgroundColor: 'rgba(255, 255, 255, 0.1)',
                transform: 'translateY(-1px)',
                transition: 'all 0.3s ease'
              },
              '&:after': isActive('about') ? {
                content: '""',
                position: 'absolute',
                bottom: '-5px',
                left: '50%',
                transform: 'translateX(-50%)',
                width: '30px',
                height: '3px',
                backgroundColor: 'white',
                borderRadius: '2px'
              } : {}
            }}
          >
            About
          </Button>
          <Button 
            onClick={() => scrollToSection('contact')}
            sx={{ 
              color: 'white',
              fontSize: {
                xs: '0.875rem',
                sm: '1rem'
              },
              position: 'relative',
              '&:hover': {
                backgroundColor: 'rgba(255, 255, 255, 0.1)',
                transform: 'translateY(-1px)',
                transition: 'all 0.3s ease'
              },
              '&:after': isActive('contact') ? {
                content: '""',
                position: 'absolute',
                bottom: '-5px',
                left: '50%',
                transform: 'translateX(-50%)',
                width: '30px',
                height: '3px',
                backgroundColor: 'white',
                borderRadius: '2px'
              } : {}
            }}
          >
            Contact
          </Button>
        </Box>

        <Box sx={{ display: { xs: 'flex', sm: 'none' } }}>
          <IconButton
            onClick={handleDrawerToggle}
            sx={{ color: 'white' }}
            aria-label="open navigation"
            size="large"
          >
            <MenuIcon />
          </IconButton>
          <Drawer
            anchor="right"
            open={mobileOpen}
            onClose={handleDrawerToggle}
            PaperProps={{ sx: { width: 240 } }}
          >
            <List sx={{ pt: 2 }}>
              <ListItemButton
                selected={isActive('home')}
                onClick={() => {
                  scrollToSection('home');
                  setMobileOpen(false);
                }}
              >
                <ListItemText primary="Home" />
              </ListItemButton>
              <ListItemButton
                selected={isActive('about')}
                onClick={() => {
                  scrollToSection('about');
                  setMobileOpen(false);
                }}
              >
                <ListItemText primary="About" />
              </ListItemButton>
              <ListItemButton
                selected={isActive('contact')}
                onClick={() => {
                  scrollToSection('contact');
                  setMobileOpen(false);
                }}
              >
                <ListItemText primary="Contact" />
              </ListItemButton>
            </List>
          </Drawer>
        </Box>
      </Toolbar>
    </Box>
  );
};

export default Header;
