'use client';

import TextField from '@mui/material/TextField';
import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Paper from '@mui/material/Paper';
import SendIcon from '@mui/icons-material/Send';
import { BUTTON_HOVER_STYLES } from '@/constants/buttonColors';

export default function ContactForm() {
  return (
    <Box
      sx={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        minHeight: '100vh',
        backgroundColor: 'background.default',
        p: 2,
      }}
    >
      <Paper
        elevation={6}
        sx={{
          width: '100%',
          maxWidth: {
            xs: '98%',
            sm: 650,
            md: 780,
            lg: 900,
          },
          p: {
            xs: 2,
            sm: 2.5,
            md: 3,
          },
          borderRadius: 3,
          backgroundColor: 'background.paper',
          boxShadow: '0 10px 30px rgba(0,0,0,0.1)',
          display: 'flex',
          flexDirection: 'column',
        }}
      >
        {/* Heading */}
        <Typography
          variant="h5"
          component="h1"
          align="center"
          sx={{
            fontWeight: 'bold',
            color: 'primary.main',
            mb: 1,
          }}
        >
          Contact Us
        </Typography>

        {/* Sub heading */}
        <Typography
          variant="body2"
          align="center"
          color="text.secondary"
          sx={{ mb: 2 }}
        >
          Fill out the form below and we&apos;ll get back to you soon
        </Typography>

        {/* Form */}
        <Stack spacing={2}>
          <TextField
            label="Full Name"
            size="small"
            fullWidth
            required
          />

          <TextField
            label="Email Address"
            type="email"
            size="small"
            fullWidth
            required
          />

          <TextField
            label="Your Message"
            multiline
            rows={3}
            size="small"
            fullWidth
            required
          />

          <Button
            variant="contained"
            size="medium"
            endIcon={<SendIcon />}
            fullWidth
            sx={{
              mt: 1,
              py: 1,
              borderRadius: 2,
              backgroundColor: 'primary.main',
              fontWeight: 'bold',
              ...BUTTON_HOVER_STYLES.primary,
            }}
          >
            Send Message
          </Button>
        </Stack>
      </Paper>
    </Box>
  );
}
